import React, { useState } from 'react';
import styled from 'styled-components';
import { useTheme } from '../../contexts/ThemeContext';
import ThemeToggle from '../../components/ThemeToggle';
import StyleEditor from '../../components/StyleEditor';

// 
const SettingsContainer = styled.div`
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
`;

// 
const Title = styled.h1`
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-color);
`;

// 
const SettingsCardContainer = styled.div`
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  margin-bottom: 2rem;
`;

// 
const SettingsCard = styled.div`
  background-color: var(--card-bg);
  padding: 1.5rem;
  border-radius: var(--border-radius, 8px);
  box-shadow: var(--card-shadow, 0 2px 5px rgba(0, 0, 0, 0.1));
  
  h2 {
    font-size: 1.25rem;
    margin-bottom: 1rem;
    font-weight: 500;
    color: var(--text-color);
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  p {
    margin-bottom: 1rem;
    color: var(--text-secondary);
    font-size: 0.9rem;
  }
  
  hr {
    margin: 1rem 0;
    border: none;
    border-top: 1px solid var(--border-color);
  }
`;

// styled-components
const SettingsGroup = styled.div`
  margin-bottom: 1.5rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 1rem;
  
  &:last-child {
    border-bottom: none;
  }
  
  h3 {
    font-size: 1rem;
    margin-bottom: 0.75rem;
    color: var(--text-color);
    font-weight: 500;
  }
`;

// 
const TabsContainer = styled.div`
  display: flex;
  border-bottom: 1px solid var(--border-color);
  margin-bottom: 1.5rem;
`;

// 
const Tab = styled.button`
  padding: 0.75rem 1.5rem;
  background: none;
  border: none;
  border-bottom: 3px solid ${props => props.active ? 'var(--primary-color)' : 'transparent'};
  color: ${props => props.active ? 'var(--primary-color)' : 'var(--text-secondary)'};
  font-weight: ${props => props.active ? '600' : '400'};
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    color: var(--primary-color);
  }
`;

// 
const SettingRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid var(--border-color);
  
  &:last-child {
    border-bottom: none;
  }
  
  label {
    color: var(--text-color);
    font-weight: 500;
  }
  
  .description {
    color: var(--text-secondary);
    font-size: 0.85rem;
    margin-top: 0.25rem;
  }
`;

// 
const Settings = () => {
  const { theme } = useTheme();
  const [activeTab, setActiveTab] = useState('appearance'); // appearance
  
  return (
    <SettingsContainer>
      <Title>
        <span aria-hidden="true"></span>
        
      </Title>
      
      {/*  */}
      <TabsContainer role="tablist" aria-label="">
        <Tab 
          role="tab"
          id="tab-appearance"
          aria-controls="tabpanel-appearance"
          aria-selected={activeTab === 'appearance'} 
          onClick={() => setActiveTab('appearance')}
        >
          
        </Tab>
        <Tab 
          role="tab"
          id="tab-advanced"
          aria-controls="tabpanel-advanced"
          aria-selected={activeTab === 'advanced'} 
          onClick={() => setActiveTab('advanced')}
        >
          
        </Tab>
        <Tab 
          role="tab"
          id="tab-account"
          aria-controls="tabpanel-account"
          aria-selected={activeTab === 'account'} 
          onClick={() => setActiveTab('account')}
        >
          
        </Tab>
      </TabsContainer>

      {/*  */}
      {activeTab === 'appearance' && (
        <div role="tabpanel" id="tabpanel-appearance" aria-labelledby="tab-appearance">
          <SettingsCardContainer>
            {/*  */}
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>
              
              <SettingsGroup>
                <SettingRow>
                  <div>
                    <label htmlFor="theme-toggle-button-instance"></label>
                    <div className="description">
                      {theme.isDark ? '' : ''}
                    </div>
                  </div>
                  <ThemeToggle id="theme-toggle-button-instance" />
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
            
            {/*  */}
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>
              
              <SettingsGroup>
                <SettingRow>
                  <div>
                    <label htmlFor="font-family-select"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <select id="font-family-select" defaultValue="system">
                    <option value="system"></option>
                    <option value="sans-serif">Sans-serif</option>
                    <option value="serif">Serif</option>
                    <option value="monospace"></option>
                  </select>
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="font-size-select"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <select id="font-size-select" defaultValue="medium">
                    <option value="small"></option>
                    <option value="medium"></option>
                    <option value="large"></option>
                  </select>
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
          </SettingsCardContainer>
          
          {/*  */}
          <SettingsCard>
            <h2>
              <span aria-hidden="true"></span>
              
            </h2>
            <p></p>
            <hr />
            <StyleEditor />
          </SettingsCard>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'advanced' && (
        <div role="tabpanel" id="tabpanel-advanced" aria-labelledby="tab-advanced">
          <SettingsCardContainer>
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>
              
              <SettingsGroup>
                <h3></h3>
                <SettingRow>
                  <div>
                    <label htmlFor="developer-mode-checkbox"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input type="checkbox" id="developer-mode-checkbox" />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="performance-mode-checkbox"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input type="checkbox" id="performance-mode-checkbox" />
                </SettingRow>
              </SettingsGroup>
              
              <SettingsGroup>
                <h3></h3>
                <SettingRow>
                  <div>
                    <label></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <button></button>
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
            
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                API 
              </h2>
              <p>API</p>
              
              <SettingsGroup>
                <SettingRow>
                  <div>
                    <label htmlFor="api-endpoint-input">API</label>
                    <div className="description">
                      API
                    </div>
                  </div>
                  <input 
                    type="text" 
                    id="api-endpoint-input"
                    defaultValue="https://api.example.com" 
                    style={{ width: 'var(--input-width-md)' }}
                  />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="api-timeout-input"></label>
                    <div className="description">
                      API
                    </div>
                  </div>
                  <input 
                    type="number" 
                    id="api-timeout-input"
                    defaultValue={30} 
                    min={5} 
                    max={120}
                    style={{ width: 'var(--input-width-sm)' }}
                  />
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
          </SettingsCardContainer>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'account' && (
        <div role="tabpanel" id="tabpanel-account" aria-labelledby="tab-account">
          <SettingsCardContainer>
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>
              
              <SettingsGroup>
                <SettingRow>
                  <div>
                    <label htmlFor="username-input"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input 
                    type="text" 
                    id="username-input"
                    defaultValue="" 
                    style={{ width: 'var(--input-width-md)' }}
                  />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="email-input"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input 
                    type="email" 
                    id="email-input"
                    defaultValue="user@example.com" 
                    style={{ width: 'var(--input-width-md)' }}
                  />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <button></button>
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
            
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>
              
              <SettingsGroup>
                <SettingRow>
                  <div>
                    <label htmlFor="email-notifications-checkbox"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input type="checkbox" id="email-notifications-checkbox" defaultChecked />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="system-notifications-checkbox"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <input type="checkbox" id="system-notifications-checkbox" defaultChecked />
                </SettingRow>
                
                <SettingRow>
                  <div>
                    <label htmlFor="notification-frequency-select"></label>
                    <div className="description">
                      
                    </div>
                  </div>
                  <select id="notification-frequency-select" defaultValue="immediate">
                    <option value="immediate"></option>
                    <option value="daily"></option>
                    <option value="weekly"></option>
                  </select>
                </SettingRow>
              </SettingsGroup>
            </SettingsCard>
          </SettingsCardContainer>
        </div>
      )}
    </SettingsContainer>
  );
};

export default Settings;