/**
 * 
 */
class Logger {
  constructor(options = {}) {
    this.prefix = options.prefix || '';
    this.enabled = options.enabled !== false;
    // 
    this.logToFile = false;
    this.logPath = options.logPath || './test.log';
  }
  
  /**
   * 
   * @param {string} level - info, warn, error
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  log(level, message, data = null) {
    if (!this.enabled) return;
    
    const timestamp = new Date().toISOString();
    const prefix = this.prefix ? `[${this.prefix}]` : '';
    const logMessage = `${timestamp} ${prefix} [${level.toUpperCase()}] ${message}`;
    
    // 
    switch (level.toLowerCase()) {
      case 'error':
        console.error(logMessage, data || '');
        break;
      case 'warn':
        console.warn(logMessage, data || '');
        break;
      case 'success':
        console.log(`%c${logMessage}`, 'color: green', data || '');
        break;
      case 'info':
      default:
        console.log(logMessage, data || '');
        break;
    }
    
    // 
    
    //  ()
    if (typeof window !== 'undefined' && typeof CustomEvent === 'function') {
      const logEvent = new CustomEvent('test-log', { 
        detail: { level, category: message, message: data ? data.toString() : '', data, timestamp }
      });
      window.dispatchEvent(logEvent);
    }
  }
  
  // 
  
  /**
   * 
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  info(message, data) {
    this.log('info', message, data);
  }
  
  /**
   * 
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  warn(message, data) {
    this.log('warn', message, data);
  }
  
  /**
   * 
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  error(message, data) {
    this.log('error', message, data);
  }
  
  /**
   * 
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  success(message, data) {
    this.log('success', message, data);
  }
  
  /**
   * 
   * @param {string} message - 
   * @param {Object} [data] - 
   */
  debug(message, data) {
    this.log('debug', message, data);
  }
  
  /**
   * 
   * @param {string} filename - 
   */
  downloadLogs(filename = 'logs.json') {
    console.log('', filename);
    return true;
  }
}

// Logger
const browserLogger = new Logger({
  prefix: 'Web',
  enabled: true
});

// Logger
export default browserLogger;