import React from 'react';
import { Button } from 'antd-mobile';
import styled from 'styled-components';
import { SearchOutline, ExclamationOutline, ContentOutline } from 'antd-mobile-icons';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-8) var(--spacing-4);
  text-align: center;
  min-height: ${props => props.fullPage ? 'calc(100vh - 100px)' : '200px'};
`;

const IconWrapper = styled.div`
  font-size: 48px;
  margin-bottom: var(--spacing-4);
  color: var(--text-secondary);
`;

const Title = styled.h3`
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-medium);
  color: var(--text);
  margin-bottom: var(--spacing-2);
`;

const Description = styled.p`
  font-size: var(--font-size-md);
  color: var(--text-secondary);
  margin-bottom: var(--spacing-4);
  max-width: 300px;
`;

const ActionButton = styled(Button)`
  margin-top: var(--spacing-2);
`;

/**
 * 
 * @param {Object} props
 * @param {'no-data' | 'no-results' | 'error' | 'empty'} props.type 
 * @param {string} props.title 
 * @param {string} props.description 
 * @param {string} props.buttonText 
 * @param {Function} props.onButtonClick 
 * @param {boolean} props.fullPage 
 */
const EmptyState = ({
  type = 'empty',
  title,
  description,
  buttonText,
  onButtonClick,
  fullPage = false
}) => {
  // 
  let defaultTitle = '';
  let defaultDescription = '';
  let icon = null;

  switch (type) {
    case 'no-data':
      defaultTitle = '';
      defaultDescription = '';
      icon = <ContentOutline />;
      break;
    case 'no-results':
      defaultTitle = '';
      defaultDescription = '';
      icon = <SearchOutline />;
      break;
    case 'error':
      defaultTitle = '';
      defaultDescription = '';
      icon = <ExclamationOutline />;
      break;
    case 'empty':
    default:
      defaultTitle = '';
      defaultDescription = '';
      icon = <ContentOutline />;
      break;
  }

  const finalTitle = title || defaultTitle;
  const finalDescription = description || defaultDescription;

  return (
    <Container fullPage={fullPage}>
      <IconWrapper>{icon}</IconWrapper>
      <Title>{finalTitle}</Title>
      <Description>{finalDescription}</Description>
      {buttonText && (
        <ActionButton color="primary" onClick={onButtonClick}>
          {buttonText}
        </ActionButton>
      )}
    </Container>
  );
};

export default EmptyState; 