import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { useTheme } from '../contexts/ThemeContext';

// 
const FooterContainer = styled.footer`
  padding: 2rem 1rem;
  background-color: var(--surface);
  border-top: 1px solid var(--border-color);
  color: var(--text-secondary);
  margin-top: auto;
`;

// 
const FooterContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
`;

// 
const FooterColumn = styled.div`
  h3 {
    font-size: 1.1rem;
    margin-bottom: 1rem;
    color: var(--text-color);
    font-weight: 500;
  }
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    margin-bottom: 0.5rem;
  }
  
  a {
    color: var(--text-secondary);
    text-decoration: none;
    transition: color 0.2s ease;
    
    &:hover {
      color: var(--primary-color);
    }
  }
`;

// 
const CopyrightSection = styled.div`
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
  text-align: center;
  font-size: 0.9rem;
  
  p {
    margin: 0.5rem 0;
  }
`;

// 
const SocialLinks = styled.div`
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  justify-content: center;
  
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: var(--background);
    color: var(--text-color);
    transition: all 0.2s ease;
    
    &:hover {
      background-color: var(--primary-color);
      color: white;
      transform: translateY(-2px);
    }
  }
`;

// 
const Footer = () => {
  const { theme } = useTheme();
  const currentYear = new Date().getFullYear();
  
  return (
    <FooterContainer>
      <FooterContent>
        <FooterColumn>
          <h3></h3>
          <ul>
            <li><Link to="/about"></Link></li>
            <li><Link to="/team"></Link></li>
            <li><Link to="/contact"></Link></li>
            <li><Link to="/careers"></Link></li>
          </ul>
        </FooterColumn>
        
        <FooterColumn>
          <h3></h3>
          <ul>
            <li><Link to="/docs"></Link></li>
            <li><Link to="/api">API</Link></li>
            <li><Link to="/examples"></Link></li>
            <li><Link to="/blog"></Link></li>
          </ul>
        </FooterColumn>
        
        <FooterColumn>
          <h3></h3>
          <ul>
            <li><Link to="/help"></Link></li>
            <li><Link to="/faq"></Link></li>
            <li><Link to="/feedback"></Link></li>
            <li><Link to="/status"></Link></li>
          </ul>
        </FooterColumn>
        
        <FooterColumn>
          <h3></h3>
          <p>: support@echo-ai.com</p>
          <p>: 400-800-8888</p>
          <p>: </p>
        </FooterColumn>
      </FooterContent>
      
      <CopyrightSection>
        <p>
           {currentYear} Echo AI  
        </p>
        <p>
          <Link to="/privacy"></Link> | 
          <Link to="/terms"> </Link> | 
          <Link to="/sitemap"> </Link>
        </p>
        
        <SocialLinks>
          <a href="https://github.com/echo-ai" target="_blank" rel="noopener noreferrer" aria-label="GitHub">
            <span role="img" aria-label="github"></span>
          </a>
          <a href="https://twitter.com/echo-ai" target="_blank" rel="noopener noreferrer" aria-label="Twitter">
            <span role="img" aria-label="twitter"></span>
          </a>
          <a href="https://linkedin.com/company/echo-ai" target="_blank" rel="noopener noreferrer" aria-label="LinkedIn">
            <span role="img" aria-label="linkedin"></span>
          </a>
          <a href="https://discord.gg/echo-ai" target="_blank" rel="noopener noreferrer" aria-label="Discord">
            <span role="img" aria-label="discord"></span>
          </a>
        </SocialLinks>
      </CopyrightSection>
    </FooterContainer>
  );
};

export default Footer; 