import { useState, useCallback, useEffect, useRef } from 'react';
import { initTTS, getBestTWVoice, speakWithFix, speakStreaming } from '../utils/TtsPolyfill';
import voiceStateManager from '../utils/VoiceStateManager';

/**
 * Custom Hook for Text-to-Speech (TTS) synthesis.
 * 
 */
const useTTS = () => {
    const [isSpeaking, setIsSpeaking] = useState(false);
    // 
    const synth = window.speechSynthesis; 
    // console.log("(TTS):", synth ? "" : "");
    
    const [availableVoices, setAvailableVoices] = useState([]);
    const [bestVoice, setBestVoice] = useState(null);
    const [activeStreaming, setActiveStreaming] = useState(null);
    const [progress, setProgress] = useState({ current: 0, total: 0 });
    
    // refspeakStream
    const speakStreamRef = useRef(null);
    const currentRequestIdRef = useRef(null);

    // 
    useEffect(() => {
        // console.log("...");
        initTTS();
    }, []);

    // 
    useEffect(() => {
        const loadVoices = () => {
            try {
                if (!synth) {
                    // console.error("");
                    return;
                }
                
                const voices = synth.getVoices();
                // console.log(` ${voices.length} `);
                
                if (voices.length === 0) {
                    // console.warn("");
                    return;
                }
                
                setAvailableVoices(voices);
                
                // 
                const bestTWVoice = getBestTWVoice();
                if (bestTWVoice) {
                    // console.log(`: ${bestTWVoice.name} (${bestTWVoice.lang})`);
                    setBestVoice(bestTWVoice);
                } else {
                    // console.warn("");
                }
            } catch (error) {
                // console.error(":", error);
            }
        };
        
        try {
            // voices
            if (synth.onvoiceschanged !== undefined) {
                // console.log("");
                synth.onvoiceschanged = loadVoices;
            }
            
            // 
            // console.log("");
            loadVoices();
            
            // setTimeout
            if (synth.getVoices().length === 0) {
                // console.log("");
                setTimeout(loadVoices, 300); // 
            }
        } catch (error) {
            // console.error(":", error);
        }
    }, [synth]);

    // 
    const selectVoice = useCallback((targetLang = 'zh-CN', preferGoogle = true) => {
        if (!availableVoices.length) {
            // console.warn("");
            return null;
        }

        // 
        let selectedVoice = null;
        
        // 1. Google(preferGoogletrue)
        if (preferGoogle) {
            selectedVoice = availableVoices.find(voice => 
                voice.name.includes('Google') && voice.lang === targetLang
            );
            
            if (selectedVoice) {
                // console.log(`Google ${targetLang}:`, selectedVoice.name);
                return selectedVoice;
            }
        }
        
        // 2. 
        selectedVoice = availableVoices.find(voice => voice.lang === targetLang);
        if (selectedVoice) {
            // console.log(`${targetLang}:`, selectedVoice.name);
            return selectedVoice;
        }
        
        // 3. (zh)
        const langMain = targetLang.split('-')[0];
        selectedVoice = availableVoices.find(voice => 
            voice.lang.startsWith(langMain + '-')
        );
        if (selectedVoice) {
            // console.log(`${langMain}:`, selectedVoice.name);
            return selectedVoice;
        }
        
        // 4. 
        // console.log(`${targetLang}`);
        return null;
    }, [availableVoices]);

    // 
    const cancel = useCallback(() => {
        if (synth) {
            // console.log("TTS");
            synth.cancel(); // Stops current utterance and clears queue
            setIsSpeaking(false);
            
            // 
            if (activeStreaming) {
                activeStreaming.cancel();
                setActiveStreaming(null);
            }
            
            // TTS
            if (currentRequestIdRef.current) {
                voiceStateManager.releaseTTS(currentRequestIdRef.current);
                currentRequestIdRef.current = null;
            }
            
            // 
            setProgress({ current: 0, total: 0 });
            
            // 
            return new Promise(resolve => setTimeout(resolve, 300));
        }
        return Promise.resolve();
    }, [synth, activeStreaming]);

    // 
    const speak = useCallback((text, lang = 'zh-CN', rate = 1.0, pitch = 1, onEnd) => {
        // 
        if (text && text.length > 80 && speakStreamRef.current && (text.includes('') || text.includes('') || text.includes('') || text.includes(''))) {
            return speakStreamRef.current(text, lang, rate, pitch, onEnd);
        }
        
        // 
        if (isSpeaking) {
            return cancel().then(() => {
                // isSpeaking
                setTimeout(() => {
                    return performSpeak();
                }, 500); // 
            });
        } else {
            return performSpeak();
        }
        
        // 
        async function performSpeak() {
            // console.log(`: "${text}", : ${lang}, : ${rate}, : ${pitch}`);
            
            // onEnd
            if (typeof lang === 'function') {
                // console.log("");
                onEnd = lang;
                lang = 'zh-CN'; // 
                rate = 1.0;
                pitch = 1;
            }
            
            // 
            if (!text || text.trim() === '') {
                // console.warn("");
                if (onEnd && typeof onEnd === 'function') {
                    setTimeout(onEnd, 10);
                }
                return false;
            }

            try {
                // TTS
                const requestId = await voiceStateManager.requestTTSStart();
                currentRequestIdRef.current = requestId;
                // console.log(`TTSID: ${requestId}`);
            } catch (error) {
                // console.error('TTS:', error);
                if (onEnd && typeof onEnd === 'function') {
                    setTimeout(onEnd, 10);
                }
                return false;
            }

            // 
            let callbackCalled = false;
            const callbackId = Date.now() + Math.random(); // ID
            
            const enhancedCallback = () => {
                if (callbackCalled) {
                    // console.log(`[TTS-${callbackId}] `);
                    return; // 
                }
                callbackCalled = true;
                
                // console.log(`[TTS-${callbackId}] TTS`);
                
                // TTS
                if (currentRequestIdRef.current) {
                    voiceStateManager.releaseTTS(currentRequestIdRef.current);
                    currentRequestIdRef.current = null;
                }
                
                setIsSpeaking(false);
                
                if (onEnd && typeof onEnd === 'function') {
                    // console.log(`[TTS-${callbackId}] `);
                    try {
                        // 
                        setTimeout(() => {
                            onEnd();
                        }, 100); // 
                    } catch (e) {
                        // console.error(`[TTS-${callbackId}] TTS:`, e);
                    }
                }
            };

            // 
            // console.log("...");
            
            // 
            setIsSpeaking(true);
            
            const speakSuccess = speakWithFix(text, bestVoice, rate, pitch, enhancedCallback);
            
            if (speakSuccess) {
                return true;
            }
            
            // 
            // console.log("...");
            
            if (!synth) {
                // console.error("");
                setIsSpeaking(false);
                if (onEnd && typeof onEnd === 'function') {
                    setTimeout(onEnd, 10);
                }
                return false;
            }
            
            // 
            // console.log(`[TTS-${callbackId}] `);
            try {
                synth.cancel();
                // 
                await new Promise(resolve => setTimeout(resolve, 100));
            } catch (e) {
                // console.warn(`[TTS-${callbackId}] :`, e);
            } 

            const utterance = new SpeechSynthesisUtterance(text);
            // console.log("");
            
            // 
            utterance.lang = lang; 
            utterance.rate = rate;
            utterance.pitch = pitch;
            // console.log(`: =${lang}, =${rate}, =${pitch}`);
            
            // (Google)
            const preferredVoice = bestVoice || selectVoice(lang, true);
            if (preferredVoice) {
                // console.log(`: ${preferredVoice.name} (${preferredVoice.lang})`);
                utterance.voice = preferredVoice;
            } else {
                // console.warn(`${lang}`);
            }

            // 
            utterance.onstart = () => {
                // console.log(`TTS: ${text}`);
                setIsSpeaking(true);
            };

            utterance.onend = () => {
                // console.log("TTS");
                enhancedCallback();
            };
            
            utterance.onerror = (event) => {
                // 
                if (event.error !== 'interrupted') {
                    // console.error("TTS:", event.error);
                } else {
                    // console.log("TTS");
                }
                
                // 
                if (event.error !== 'interrupted') {
                    enhancedCallback();
                }
            };

            // 
            const timeoutMs = Math.min(3000 + text.length * 80, 30000); // 3 + 80ms30
            const safetyTimeout = setTimeout(() => {
                // console.warn(`[TTS-${callbackId}] TTS(${timeoutMs}ms)`);
                if (synth && synth.speaking) {
                    synth.cancel();
                }
                enhancedCallback();
            }, timeoutMs);

            // 
            const playTimeout = setTimeout(() => {
                try {
                    // 
                    if (callbackCalled) {
                        // console.log(`[TTS-${callbackId}] `);
                        return;
                    }
                    
                    // 
                    // console.log(`[TTS-${callbackId}] TTS speak`);
                    synth.speak(utterance);

                    // 
                    setTimeout(() => {
                        if (synth.speaking && !callbackCalled) {
                            setIsSpeaking(true);
                        }
                    }, 50);
                    
                    // 
                    const speakingCheckTimer = setTimeout(() => {
                        if (!synth.speaking && !callbackCalled) {
                            // console.warn(`[TTS-${callbackId}] TTS`);
                            enhancedCallback();
                        }
                    }, 1000); // 
                    
                    // 
                    return () => {
                        clearTimeout(safetyTimeout);
                        clearTimeout(speakingCheckTimer);
                    };
                } catch (error) {
                    // console.error(`[TTS-${callbackId}] :`, error);
                    clearTimeout(safetyTimeout);
                    enhancedCallback();
                }
            }, 150); // 
            
            // 
            return () => {
                clearTimeout(playTimeout);
                clearTimeout(safetyTimeout);
                if (synth && synth.speaking) {
                    synth.cancel();
                }
            };

            return true;
        }
    }, [synth, isSpeaking, selectVoice, bestVoice, cancel]);

    // 
    const speakStream = useCallback((text, lang = 'zh-CN', rate = 1.0, pitch = 1, onEnd) => {
        // 
        if (isSpeaking || activeStreaming) {
            cancel().then(() => {
                // 
                setTimeout(() => {
                    startStreamingAfterCancel();
                }, 500);  // 
            });
            return true;  // 
        } else {
            return startStreamingAfterCancel();
        }
        
        // 
        async function startStreamingAfterCancel() {
            // console.log(`: "${text.substring(0, 30)}${text.length > 30 ? '...' : ''}", : ${lang}`);
            
            // onEnd
            if (typeof lang === 'function') {
                // console.log("");
                onEnd = lang;
                lang = 'zh-CN'; // 
                rate = 1.0;
                pitch = 1;
            }
            
            // 
            if (!text || text.trim() === '') {
                // console.warn("");
                if (onEnd && typeof onEnd === 'function') {
                    setTimeout(onEnd, 5);
                }
                return false;
            }

            try {
                // TTS
                const requestId = await voiceStateManager.requestTTSStart();
                currentRequestIdRef.current = requestId;
                // console.log(`TTSID: ${requestId}`);
            } catch (error) {
                // console.error('TTS:', error);
                if (onEnd && typeof onEnd === 'function') {
                    setTimeout(onEnd, 5);
                }
                return false;
            }

            // 
            const maxTextLength = 2000; // 
            let processedText = text;
            
            if (text.length > maxTextLength) {
                // console.warn(`(${text.length})(${maxTextLength})`);
                processedText = text.substring(0, maxTextLength);
            }
            
            // 
            setIsSpeaking(true);
            
            // 
            let callbackCalled = false;
            
            // 
            const safeCallback = () => {
                if (callbackCalled) return;
                callbackCalled = true;
                
                // console.log("");
                
                // TTS
                if (currentRequestIdRef.current) {
                    voiceStateManager.releaseTTS(currentRequestIdRef.current);
                    currentRequestIdRef.current = null;
                }
                
                setIsSpeaking(false);
                setActiveStreaming(null);
                setProgress({ current: 0, total: 0 });
                
                if (onEnd && typeof onEnd === 'function') {
                    // 
                    setTimeout(() => {
                        try {
                            onEnd();
                        } catch (error) {
                            // console.error(":", error);
                        }
                    }, 200);
                }
            };
            
            // 
            let streamingTimeout = setTimeout(() => {
                // console.warn("");
                safeCallback();
            }, Math.min(text.length * 300, 180000)); // 300ms3
            
            try {
                const streamController = speakStreaming(
                    processedText, 
                    bestVoice, 
                    rate, 
                    pitch,
                    // 
                    (current, total) => {
                        // console.log(`: ${current + 1}/${total}`);
                        setProgress({ current: current + 1, total });
                    },
                    // 
                    () => {
                        clearTimeout(streamingTimeout); // 
                        // console.log("");
                        safeCallback();
                    }
                );
                
                // 
                if (streamController) {
                    setActiveStreaming(streamController);
                    setProgress({ current: 0, total: streamController.segmentsCount });
                    return true;
                } else {
                    // 
                    // console.warn("");
                    return speak(processedText, lang, rate, pitch, onEnd);
                }
            } catch (error) {
                // console.error(":", error);
                clearTimeout(streamingTimeout);
                setIsSpeaking(false);
                
                // 
                // console.warn("");
                return speak(processedText, lang, rate, pitch, onEnd);
            }
        }
    }, [bestVoice, isSpeaking, activeStreaming, cancel, speak]);

    // Effect to handle browser visibility change and cleanup
    useEffect(() => {
        // ChromeSpeechSynthesis
        const handleVisibilityChange = () => {
            if (document.hidden && synth && synth.speaking) {
                // console.log("");
                synth.pause();
                synth.cancel();
                setIsSpeaking(false);
                
                // 
                if (activeStreaming) {
                    activeStreaming.cancel();
                    setActiveStreaming(null);
                }
        }
        };
        
        document.addEventListener('visibilitychange', handleVisibilityChange);
        // console.log("visibilitychange");
    
        // 
        // console.log("");
        const intervalId = setInterval(() => {
            if (synth && synth.speaking && !isSpeaking) {
                // console.log("");
                setIsSpeaking(true);
            } else if (synth && !synth.speaking && isSpeaking && !activeStreaming) {
                // console.log("");
                setIsSpeaking(false);
            }
        }, 1000);
        
        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
            clearInterval(intervalId);
            // console.log("");
            
            if (synth && isSpeaking) {
                // console.log("TTS");
                synth.cancel();
            }
            
            if (activeStreaming) {
                activeStreaming.cancel();
            }
        };
    }, [synth, isSpeaking, activeStreaming]);

    // speakStreamRef.current
    useEffect(() => {
        speakStreamRef.current = speakStream;
    }, [speakStream]);

    return {
        speak,
        speakStream,
        cancel,
        isSpeaking,
        progress
    };
};

export default useTTS;