import { useState, useCallback, useEffect, useRef } from 'react';
import { checkMicrophonePermission } from '../utils/microphoneUtils';
import voiceStateManager from '../utils/VoiceStateManager';

/**
 * 
 * Web Speech API
 * @returns {Object} 
 */
const useVoice = () => {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState('');
  const [error, setError] = useState(null);
  const recognitionRef = useRef(null);
  const retryCountRef = useRef(0);
  const maxRetries = 3;
  const isStoppingRef = useRef(false); // ref
  const currentRequestIdRef = useRef(null);

  // 
  useEffect(() => {
    // 
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    
    if (SpeechRecognition) {
      const recognition = new SpeechRecognition();
      
      // 
      recognition.continuous = false; // 
      recognition.lang = 'zh-CN'; // 
      recognition.interimResults = false; // 
      recognition.maxAlternatives = 1; // 
      
      //  ref 
      recognitionRef.current = recognition;
      
      // 
      return () => {
        if (recognitionRef.current) {
          try {
            // 
            if (isListening) {
              recognitionRef.current.stop();
            }
          } catch (e) {
            // console.warn('Error stopping recognition on cleanup:', e);
          }
        }
      };
    } else {
      // console.error('Speech recognition not supported in this browser');
      setError('');
    }
  }, [isListening]);



  // 
  const safeStopRecognition = useCallback(() => {
    return new Promise(resolve => {
      if (!recognitionRef.current) {
        // console.log("[useVoice] ");
        setIsListening(false);
        isStoppingRef.current = false;
        resolve();
        return;
      }
      
      // 
      if (isStoppingRef.current) {
        // console.log("[useVoice] ");
        const checkInterval = setInterval(() => {
          if (!isStoppingRef.current) {
            clearInterval(checkInterval);
            resolve();
          }
        }, 50);
        // 2
        setTimeout(() => {
          clearInterval(checkInterval);
          isStoppingRef.current = false;
          resolve();
        }, 2000);
        return;
      }
      
      // 
      if (!isListening) {
        // console.log("[useVoice] ");
        resolve();
        return;
      }
      
      // 
      isStoppingRef.current = true;
      
      // 
      const stopTimeout = setTimeout(() => {
        // console.warn("[useVoice] ");
        isStoppingRef.current = false;
        setIsListening(false);
        resolve();
      }, 3000);
      
      // onendpromise
      const originalOnEnd = recognitionRef.current.onend;
      recognitionRef.current.onend = (event) => {
        clearTimeout(stopTimeout);
        // onend
        if (originalOnEnd && typeof originalOnEnd === 'function') {
          try {
            originalOnEnd(event);
          } catch (e) {
            // console.warn("[useVoice] onend:", e);
          }
        }
        
        // 
        isStoppingRef.current = false;
        // console.log('[useVoice] ');
        setIsListening(false);
        
        // STT
        if (currentRequestIdRef.current) {
          voiceStateManager.releaseSTT(currentRequestIdRef.current);
          currentRequestIdRef.current = null;
        }
        
        resolve();
      };
      
      try {
        // console.log("[useVoice] ...");
        recognitionRef.current.stop();
      } catch (e) {
        // console.log("[useVoice]  ():", e);
        clearTimeout(stopTimeout);
        isStoppingRef.current = false;
        setIsListening(false);
        resolve();
      }
    });
  }, [isListening]);

  // 
  const startListening = useCallback(async () => {
    // console.log("[useVoice] Attempting to start listening...");
    
    //  - 
    if (isListening || isStoppingRef.current) {
      // console.log(`[useVoice] : isListening=${isListening}, isStopping=${isStoppingRef.current}`);
      return;
    }
    
    // 
    if (currentRequestIdRef.current) {
      // console.log('[useVoice] STT');
      return;
    }
    
    setError(null);
    setTranscript('');
    
    if (!recognitionRef.current) {
      // console.error("[useVoice] ");
      setError('');
      return;
    }
    
    // 
    const hasPermission = await checkMicrophonePermission();
    if (!hasPermission) {
      // console.error("[useVoice] ");
      setError('');
      return;
    }
    
    try {
       // STT
       const requestId = await voiceStateManager.requestSTTStart();
       currentRequestIdRef.current = requestId;
      // 
      // 
      recognitionRef.current.onstart = () => {
        // console.log(`[useVoice] Voice recognition started with ID: ${requestId}`);
        retryCountRef.current = 0; // 
        setIsListening(true);
      };
      
      // 
      recognitionRef.current.onend = () => {
        // console.log(`[useVoice] Voice recognition ended for ${requestId}`);
        isStoppingRef.current = false;
        setIsListening(false);
        
        // 
        if (currentRequestIdRef.current === requestId) {
          voiceStateManager.releaseSTT(requestId);
          currentRequestIdRef.current = null;
        }
      };

      // 
      recognitionRef.current.onresult = (event) => {
        if (event.results.length > 0) {
          const result = event.results[0][0].transcript;
          // console.log('[useVoice] Voice recognition result:', result);
          setTranscript(result);
        } else {
          // console.warn('[useVoice] ');
        }
      };
      
      // 
      recognitionRef.current.onerror = (event) => {
        // console.error(`[useVoice] Voice recognition error for ${requestId}:`, event.error);
        
        let errorMessage = '';
        let shouldRetry = false;
        
        // 
        switch (event.error) {
          case 'no-speech':
            errorMessage = '';
            shouldRetry = true; // 
            break;
          case 'audio-capture':
            errorMessage = '';
            break;
          case 'not-allowed':
            errorMessage = '';
            break;
          case 'network':
            errorMessage = '';
            shouldRetry = true; // 
            break;
          case 'aborted':
            // 
            // console.log('[useVoice] ');
            // 
            if (currentRequestIdRef.current === requestId) {
              voiceStateManager.releaseSTT(requestId);
              currentRequestIdRef.current = null;
            }
            return;
          default:
            errorMessage = `: ${event.error}`;
            shouldRetry = true; // 
        }
        
        setError(errorMessage);
        setIsListening(false);
        
        // 
        if (currentRequestIdRef.current === requestId) {
          voiceStateManager.releaseSTT(requestId);
          currentRequestIdRef.current = null;
        }
        
        // 
        if (shouldRetry && retryCountRef.current < maxRetries) {
          // console.log(`[useVoice]  (${retryCountRef.current + 1}/${maxRetries})...`);
          retryCountRef.current += 1;
          
          // 
          setTimeout(() => {
            try {
              recognitionRef.current.start();
            } catch (e) {
              // console.error('[useVoice] :', e);
            }
          }, 1000);
        } else if (retryCountRef.current >= maxRetries) {
          // console.error('[useVoice] ');
          setError('');
        }
      };

      // 
      // console.log(`[useVoice] ID: ${requestId}`);
      
      // 
      const startTimeout = setTimeout(() => {
        // 
        if (isListening || isStoppingRef.current || currentRequestIdRef.current !== requestId) {
          // console.log("[useVoice] ");
          // STT
          if (currentRequestIdRef.current === requestId) {
            voiceStateManager.releaseSTT(requestId);
            currentRequestIdRef.current = null;
          }
          return;
        }
        
        try {
          // console.log("[useVoice]  recognition.start()...");
          recognitionRef.current.start();
        } catch (error) {
          // console.error('[useVoice] :', error);
          
          // STT
          if (currentRequestIdRef.current === requestId) {
            voiceStateManager.releaseSTT(requestId);
            currentRequestIdRef.current = null;
          }
          
          // 
          if (error.message && error.message.includes('already started')) {
            // console.log("[useVoice] ");
            setIsListening(true);
          } else {
            setError(': ' + error.message);
            setIsListening(false);
          }
        }
      }, 500); // 
      
      // 
      return () => {
        clearTimeout(startTimeout);
      };
      
    } catch (error) {
      // console.error('[useVoice] STT:', error);
      // TTS
      if (!error.message.includes('TTS is currently active') && 
          !error.message.includes('STT request timeout')) {
        setError(error.message);
      } else {
        // console.log('[useVoice] TTS');
      }
      setIsListening(false);
    }
  }, [checkMicrophonePermission, safeStopRecognition, isListening]);

  // 
  const stopListening = useCallback(async () => {
    // console.log("[useVoice] Attempting to stop listening...");
    retryCountRef.current = 0; // 
    
    await safeStopRecognition();
  }, [safeStopRecognition]);

  // 
  const reset = useCallback(() => {
    setTranscript('');
    setError(null);
    retryCountRef.current = 0;
    stopListening();
  }, [stopListening]);

  return {
    isListening,
    transcript,
    error,
    startListening,
    stopListening,
    reset
  };
};

export default useVoice;