import React, { createContext, useState, useEffect } from 'react';
import { ThemeProvider as StyledThemeProvider } from 'styled-components';
import { ThemeProvider as ContextThemeProvider } from '../contexts/ThemeContext';

// CSS
const themeObject = {
  background: 'var(--background)',
  surface: 'var(--surface)',
  text: 'var(--text)',
  textSecondary: 'var(--text-secondary)',
  border: 'var(--border)',
  primary: 'var(--color-primary)',
  primaryLight: 'var(--color-primary-light)',
  primaryDark: 'var(--color-primary-dark)',
  secondary: 'var(--color-secondary)',
  secondaryLight: 'var(--color-secondary-light)',
  secondaryDark: 'var(--color-secondary-dark)',
  success: 'var(--color-success)',
  warning: 'var(--color-warning)',
  error: 'var(--color-error)',
  info: 'var(--color-info)',
  shadow: 'var(--shadow)'
};

// 
export const ThemeContext = createContext({
  theme: 'light',
  toggleTheme: () => {},
});

//  -  contexts/ThemeContext 
const LegacyThemeProvider = ({ children }) => {
  // 
  const [theme, setTheme] = useState(() => {
    const savedTheme = localStorage.getItem('theme');
    return savedTheme || 'light';
  });

  // 
  const toggleTheme = () => {
    setTheme(prevTheme => {
      const newTheme = prevTheme === 'light' ? 'dark' : 'light';
      localStorage.setItem('theme', newTheme);
      return newTheme;
    });
  };

  // data-theme
  useEffect(() => {
    document.documentElement.setAttribute('data-theme', theme);
  }, [theme]);

  // 
  const themeContextValue = {
    theme,
    toggleTheme,
  };

  // StyledThemeProviderstyled-components
  return (
    <ThemeContext.Provider value={themeContextValue}>
      <StyledThemeProvider theme={themeObject}>
        {children}
      </StyledThemeProvider>
    </ThemeContext.Provider>
  );
};

// contexts/ThemeContextThemeProvider
export default ContextThemeProvider; 