/**
 * TTS 
 * 
 * ChromeFirefoxAPI:
 * 1. Chrome
 * 2. Firefox
 * 3. 
 * 4. 
 */

// 
export function initTTS() {
  try {
    console.log("TTS...");
    const synth = window.speechSynthesis;
    
    if (!synth) {
      console.error("API");
      return false;
    }
    
    // 
    synth.cancel();
    
    // Chrome bug: 
    // https://bugs.chromium.org/p/chromium/issues/detail?id=679437
    // : 
    const intervalId = setInterval(() => {
      if (!synth.speaking) return;
      
      // 
      synth.pause();
      setTimeout(() => {
        if (synth) synth.resume();
      }, 50); // 
    }, 2000); // 2
    
    // interval ID
    window._ttsCheckIntervalId = intervalId;
    
    // voices
    if (synth.getVoices().length === 0) {
      console.log("");
      setTimeout(() => {
        // 
        const voices = synth.getVoices();
        console.log(` ${voices.length} `);
        
        if (voices.length === 0) {
          console.warn("");
          // 
          setTimeout(() => {
            const retryVoices = synth.getVoices();
            console.log(` ${retryVoices.length} `);
          }, 1000);
        }
      }, 500); // 
    }
    
    // 
    try {
      const utterance = new SpeechSynthesisUtterance('');
      utterance.volume = 0; // 
      utterance.rate = 1;
      synth.speak(utterance);
      console.log("");
    } catch (e) {
      console.warn("", e);
    }
    
    console.log("TTS");
    return true;
  } catch (error) {
    console.error("TTS:", error);
    return false;
  }
}

// 
export function getBestTWVoice() {
  try {
    const synth = window.speechSynthesis;
    if (!synth) return null;
    
    const voices = synth.getVoices();
    if (!voices || voices.length === 0) return null;
    
    // Google
    let bestVoice = voices.find(v => v.name.includes('Google') && v.lang === 'zh-CN');
    
    // GoogleGoogle
    if (!bestVoice) {
      bestVoice = voices.find(v => v.name.includes('Google') && v.lang === 'zh-TW');
    }
    
    // Googlezh-CN
    if (!bestVoice) {
      bestVoice = voices.find(v => v.lang === 'zh-CN');
    }
    
    // zh-TW
    if (!bestVoice) {
      bestVoice = voices.find(v => v.lang === 'zh-TW');
    }
    
    // zh
    if (!bestVoice) {
      bestVoice = voices.find(v => v.lang.startsWith('zh'));
    }
    
    return bestVoice;
  } catch (error) {
    console.error(":", error);
    return null;
  }
}

// 
export function splitTextForStreaming(text) {
  if (!text || text.length <= 80) return [text]; // 
  
  // 
  const sentenceBreaks = text.match(/[.?!;][,\s]?/g) || [];
  
  if (sentenceBreaks.length === 0) {
    // 
    const parts = text.split(/[,\s]/g).filter(t => t.trim());
    
    // 
    const result = [];
    let currentSegment = '';
    
    for (const part of parts) {
      if (currentSegment.length + part.length < 50) { // 
        currentSegment += part + (part.length > 0 ? '' : '');
      } else {
        if (currentSegment) result.push(currentSegment);
        currentSegment = part;
      }
    }
    
    if (currentSegment) result.push(currentSegment);
    return result;
  }
  
  const segments = [];
  let lastIndex = 0;
  
  // 
  for (let i = 0; i < sentenceBreaks.length; i++) {
    // 
    const breakIndex = text.indexOf(sentenceBreaks[i], lastIndex);
    if (breakIndex === -1) continue;
    
    // 
    const endIndex = breakIndex + sentenceBreaks[i].length;
    
    // 
    const segment = text.substring(lastIndex, endIndex);
    if (segment.trim()) {
      segments.push(segment);
    }
    
    // 
    lastIndex = endIndex;
  }
  
  // 
  if (lastIndex < text.length) {
    const lastSegment = text.substring(lastIndex);
    if (lastSegment.trim()) {
      segments.push(lastSegment);
    }
  }
  
  // 
  const result = [];
  let currentSegment = '';
  
  for (const segment of segments) {
    // 
    if (segment.length > 80) {
      // 
      if (currentSegment) {
        result.push(currentSegment);
        currentSegment = '';
      }
      
      // 
      const subBreaks = segment.match(/[,]/g) || [];
      
      if (subBreaks.length === 0) {
        // 
        result.push(segment);
      } else {
        let subLastIndex = 0;
        let subPart = '';
        
        // 
        for (let j = 0; j < subBreaks.length; j++) {
          const subBreakIndex = segment.indexOf(subBreaks[j], subLastIndex);
          if (subBreakIndex === -1) continue;
          
          const subEndIndex = subBreakIndex + subBreaks[j].length;
          const part = segment.substring(subLastIndex, subEndIndex);
          
          if (subPart.length + part.length < 80) {
            subPart += part;
          } else {
            if (subPart) result.push(subPart);
            subPart = part;
          }
          
          subLastIndex = subEndIndex;
        }
        
        // 
        if (subLastIndex < segment.length) {
          const lastPart = segment.substring(subLastIndex);
          if (subPart.length + lastPart.length < 80) {
            subPart += lastPart;
          } else {
            if (subPart) result.push(subPart);
            subPart = lastPart;
          }
        }
        
        if (subPart) result.push(subPart);
      }
    } 
    // 80
    else if (currentSegment.length + segment.length < 80) {
      currentSegment += segment;
    } else {
      // 
      if (currentSegment) result.push(currentSegment);
      currentSegment = segment;
    }
  }
  
  // 
  if (currentSegment) result.push(currentSegment);
  
  return result;
}

// 
export function speakStreaming(text, voice = null, rate = 1.3, pitch = 1, onSegmentEnd = null, onAllEnd = null) {
  // 
  const segments = splitTextForStreaming(text);
  
  if (!segments || segments.length === 0) {
    console.warn('');
    if (onAllEnd) setTimeout(onAllEnd, 0);
    return false;
  }
  
  console.log(` ${segments.length} `);
  
  // 
  const synth = window.speechSynthesis;
  const utterances = segments.map((segment, index) => {
    const utterance = new SpeechSynthesisUtterance(segment);
    utterance.rate = rate;
    utterance.pitch = pitch;
    
    // 
    if (voice) {
      utterance.voice = voice;
    } else {
      const bestVoice = getBestTWVoice();
      if (bestVoice) {
        utterance.voice = bestVoice;
        utterance.lang = bestVoice.lang;
      } else {
        utterance.lang = 'zh-CN';
      }
    }
    
    return utterance;
  });
  
  let isCancelled = false;
  let lastSegmentIndex = -1; // 
  
  const cancelStreaming = () => {
    console.log('');
    isCancelled = true;
    window.speechSynthesis.cancel();
  };
  
  // 
  const streamingTimeoutId = setTimeout(() => {
    if (!isCancelled) {
      console.warn('');
      cancelStreaming();
      if (onAllEnd) onAllEnd();
    }
  }, Math.min(segments.length * 15000, 120000)); // 152
  
  // 
  const playNext = (index) => {
    if (isCancelled || index >= utterances.length) {
      if (!isCancelled && onAllEnd) {
        clearTimeout(streamingTimeoutId);
        console.log('');
        onAllEnd();
      }
      return;
    }
    
    const utterance = utterances[index];
    console.log(` ${index + 1}/${utterances.length} : "${segments[index].substring(0, 30)}${segments[index].length > 30 ? '...' : ''}"`);
    
    // 
    const segmentTimeout = setTimeout(() => {
      console.warn(` ${index + 1} `);
      synth.cancel();
      
      if (onSegmentEnd) onSegmentEnd(index, segments.length);
      lastSegmentIndex = index;
      
      // 
      setTimeout(() => playNext(index + 1), 10);
    }, Math.min(segments[index].length * 300 + 5000, 20000)); // 300ms+520
    
    // 
    utterance.onend = () => {
      clearTimeout(segmentTimeout);
      if (onSegmentEnd) onSegmentEnd(index, segments.length);
      lastSegmentIndex = index;
      
      // 
      playNext(index + 1);
    };
    
    utterance.onerror = (event) => {
      clearTimeout(segmentTimeout);
      console.error(` ${index + 1} :`, event.error);
      
      // 
      setTimeout(() => playNext(index + 1), 10);
    };
    
    // 
    try {
      // 
      if (index > 0) {
        synth.cancel();
      }
      
      synth.speak(utterance);
    } catch (error) {
      console.error(` ${index + 1} :`, error);
      
      // 
      setTimeout(() => playNext(index + 1), 10);
    }
  };
  
  // 
  playNext(0);
  
  return { 
    cancel: cancelStreaming,
    segmentsCount: segments.length
  };
}

// ChromeSafariFirefoxspeak
export function speakWithFix(text, voice = null, rate = 1.3, pitch = 1, onEnd = null) {
  try {
    const synth = window.speechSynthesis;
    if (!synth) {
      console.error("");
      if (onEnd) onEnd();
      return false;
    }
    
    // 
    synth.cancel();
    
    if (!text || text.trim() === '') {
      console.warn("");
      if (onEnd) onEnd();
      return false;
    }
    
    // 
    const utterance = new SpeechSynthesisUtterance(text);
    
    // 
    utterance.rate = rate;
    utterance.pitch = pitch;
    
    // zh-CN
    if (voice) {
      utterance.voice = voice;
    } else {
      // 
      const bestVoice = getBestTWVoice();
      if (bestVoice) {
        console.log(`: ${bestVoice.name} (${bestVoice.lang})`);
        utterance.voice = bestVoice;
        utterance.lang = bestVoice.lang;
      } else {
        // zh-CN
        console.warn("zh-CN");
        utterance.lang = 'zh-CN';
      }
    }
    
    // 
    utterance.onstart = () => {
      console.log(`TTS: ${text.substring(0, 30)}${text.length > 30 ? '...' : ''}`);
    };
    
    utterance.onend = () => {
      console.log("TTS");
      if (onEnd) {
        try {
          onEnd(); // setTimeout
        } catch (e) {
          console.error("TTS onend:", e);
        }
      }
    };
    
    utterance.onerror = (event) => {
      // 
      if (event.error !== 'interrupted') {
        console.error("TTS:", event.error);
      } else {
        console.log("TTS:", event.error);
      }
      
      if (onEnd) {
        try {
          onEnd(); // setTimeout
        } catch (e) {
          console.error("TTS onerror:", e);
        }
      }
    };
    
    // 
    setTimeout(() => {
      try {
        synth.speak(utterance);
        console.log("TTS speak");
        
        // Chrome bug: onend
        if (navigator.userAgent.indexOf('Chrome') !== -1) {
          // 
          //  + 
          let baseTimeout = 1500; // 
          let charTimeout = 200; // 
          let maxDuration = baseTimeout + text.length * charTimeout; 
          
          // Chrome
          maxDuration = Math.min(maxDuration, 20000); // 20
          
          // 
          maxDuration = Math.max(maxDuration, 2000); // 2

          console.log(`[TtsPolyfill] speakWithFix: Calculated Chrome timeout: ${maxDuration}ms for ${text.length} chars`);
          
          const chromeTimeout = setTimeout(() => {
            //  synth 
            if (window.speechSynthesis && window.speechSynthesis.speaking && !window.speechSynthesis.paused) {
              console.log("Chrome");
              try {
                synth.cancel();
              } catch (e) {
                console.warn("TTS:", e);
              }
              
              if (onEnd) {
                try {
                  onEnd(); // 
                } catch (e) {
                  console.error("TTS :", e);
                }
              }
            }
          }, maxDuration);
          
          // 
          return () => {
            clearTimeout(chromeTimeout);
          };
        }
        
        return true;
      } catch (error) {
        console.error("TTS:", error);
        if (onEnd) {
          try {
            onEnd(); // 
          } catch (e) {
            console.error("TTS :", e);
          }
        }
        return false;
      }
    }, 10); // 10ms
    
    return true;
  } catch (error) {
    console.error("TTS:", error);
    if (onEnd) {
      try {
        onEnd(); // 
      } catch (e) {
        console.error("TTS :", e);
      }
    }
    return false;
  }
}

const TtsPolyfill = {
  initTTS,
  getBestTWVoice,
  speakWithFix,
  speakStreaming,
  splitTextForStreaming
}; 

export default TtsPolyfill;