import React, { useContext } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';
import { DotLoading } from 'antd-mobile';
import styled from 'styled-components';

const LoadingContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--background);
  color: var(--text);
  font-size: var(--font-size-lg);
`;

/**
 * 
 * @param {Object} props
 * @param {React.ReactNode} props.children 
 * @param {boolean} props.requireAdmin 
 * @param {string} props.requireRole 'user', 'developer', 'admin'
 */
const ProtectedRoute = ({ children, requireAdmin = false, requireRole = null }) => {
  const { isAuthenticated, user, loading, role } = useContext(AuthContext);
  const location = useLocation();

  // 
  if (loading) {
    return (
      <LoadingContainer>
        <DotLoading />
      </LoadingContainer>
    );
  }

  // 
  if (!isAuthenticated) {
    return <Navigate to="/auth" state={{ from: location.pathname }} replace />;
  }

  // 
  if (requireAdmin && user?.role !== 'admin') {
    return <Navigate to="/unauthorized" replace />;
  }

  // 
  if (requireRole && role !== requireRole && role !== 'admin') {
    // 
    return <Navigate to="/unauthorized" replace />;
  }

  // 
  return children;
};

export default ProtectedRoute; 