// TestPage.js - AI

import React from 'react';
import styled from 'styled-components';
import { Tabs } from 'antd-mobile';
import TestRunner from '../test-utils/TestRunner';

const TestPageContainer = styled.div`
  min-height: 100vh;
  padding: 20px;
  background-color: var(--background);
  
  @media (min-width: 768px) {
    padding: 40px;
  }
`;

const Header = styled.header`
  margin-bottom: 30px;
  
  h1 {
    font-size: var(--font-size-3xl);
    color: var(--text);
    margin-bottom: 10px;
  }
  
  p {
    color: var(--text-secondary);
    max-width: 800px;
  }
`;

const TabContent = styled.div`
  margin-top: 20px;
`;

/**
 * 
 * 
 */
const TestPage = () => {
  return (
    <TestPageContainer>
      <Header>
        <h1>Echo </h1>
        <p>
          
          
        </p>
      </Header>
      
      <Tabs>
        <Tabs.Tab title="" key="e2e">
          <TabContent>
            <TestRunner />
          </TabContent>
        </Tabs.Tab>
        <Tabs.Tab title="" key="unit">
          <TabContent>
            <p></p>
            <pre>
              cd frontend<br />
              npm test
            </pre>
            <p></p>
            <pre>
              npm test -- -t ""
            </pre>
          </TabContent>
        </Tabs.Tab>
        <Tabs.Tab title="" key="manual">
          <TabContent>
            <p></p>
            <ol>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ol>
          </TabContent>
        </Tabs.Tab>
      </Tabs>
    </TestPageContainer>
  );
};

export default TestPage; 